import { Zone } from './predict';

export const zoneCounterRef = { current: 0 };

export type ZoneOptions = {
  id?: Zone['id'];
  name: Zone['name'];
  active?: Zone['active'];
  happened?: Zone['happened'];
};

export function createZone({
  id,
  name,
  active = true,
  happened = false,
}: ZoneOptions): Zone {
  const zone: Zone = {
    id: id || `${name}-${++zoneCounterRef.current}`,
    name,
    active,
    happened,
  };

  return zone;
}
