const STORAGE_KEY_PREFIX = 'predict';
const STORAGE_TEST_KEY = `${STORAGE_KEY_PREFIX}/test-key`;

type Storage<TData> = {
  get: () => TData | null;
  set: (value: TData) => void;
  remove: () => void;
};

function isStorageSupported(storage: globalThis.Storage) {
  try {
    storage.setItem(STORAGE_TEST_KEY, '');
    storage.removeItem(STORAGE_TEST_KEY);

    return true;
  } catch (error) {
    return false;
  }
}

export function createInMemoryStorage<TData>(): Storage<TData> {
  return {
    get() {
      return null;
    },
    set() {},
    remove() {},
  };
}

type StorageProps = {
  key: string;
  storage: globalThis.Storage;
};

function createStorage<TData>({ key, storage }: StorageProps): Storage<TData> {
  if (!isStorageSupported(storage)) {
    return createInMemoryStorage();
  }

  const storageKey = `${STORAGE_KEY_PREFIX}/${key}`;

  return {
    get() {
      const item = storage.getItem(storageKey);
      return item ? JSON.parse(item) : null;
    },
    set(value: TData) {
      storage.setItem(storageKey, JSON.stringify(value));
    },
    remove() {
      storage.removeItem(storageKey);
    },
  };
}

export function createSessionStorage<TData>({
  key,
}: Pick<StorageProps, 'key'>): Storage<TData> {
  return createStorage({ key, storage: sessionStorage });
}

export function createLocalStorage<TData>({
  key,
}: Pick<StorageProps, 'key'>): Storage<TData> {
  return createStorage({ key, storage: localStorage });
}
