import { computeCartAbandonment } from './lib/computeCartAbandonment';
import { invariant } from './lib/invariant';
import { logDebug } from './lib/logDebug';
import { unwrapPrediction } from './lib/unwrapPrediction';
import { initPredictTools } from './tools';
import {
  type Affinity,
  type FunnelStage,
  type OrderValue,
  type Property,
  type Segment,
  type UserProfile,
} from './types';
import { version } from './version';

export type PredictApi = {
  affinities: Affinity[];
  cartAbandonment: number;
  funnelStage: FunnelStage[];
  orderValue: OrderValue | undefined;
  segments: Segment[];
  properties: Property[];
  userProfile: UserProfile;
  zones: Zone[];
};

export type Zone = {
  id: string;
  name: string;
  active: boolean;
  happened: boolean;
};

type EventType = 'UPDATED' | 'ZONE_ADDED' | 'ZONE_REMOVED' | 'ZONE_UPDATED';
type Subscriber = (params: PredictApi & { eventType: EventType }) => void;

export type PredictCoreApi = {
  getAffinities: () => Affinity[];
  getFunnelStage: () => FunnelStage[];
  getOrderValue: () => OrderValue | undefined;
  getCartAbandonment: () => number;
  getSegments: () => Segment[];
  getProperties: () => Property[];
  getUserProfile: () => UserProfile;
  getZones: () => Zone[];
  update: (nextParams: Partial<PredictOptions>) => void;
  addZone: (zone: Zone) => Zone;
  removeZone: (zone: Zone) => void;
  updateZone: (zone: Pick<Zone, 'id'> & Partial<Zone>) => Zone;
  subscribe: (subscriber: Subscriber) => () => void;
};

export type PredictOptions = {
  userProfile: UserProfile;
};

export function predict(params: PredictOptions): PredictCoreApi {
  const PREDICT_TOOLS_ENABLED =
    (__DEV__ || __PREDICT_TOOLS__) && typeof window !== 'undefined';

  let subscribers: Subscriber[] = [];
  let userProfile = params.userProfile;
  const zones: Zone[] = [];
  let isPersonificationEnabled = false;
  /**
   * Maintains a queue of updates that need to be applied once the personification ends.
   *
   * We ignore `update()` calls when personifying to give precedence to the personified user profile.
   * So we need to apply the updates at the end of the personification to ensure
   * the latest changes are reflected.
   */
  const pendingUpdates: Array<Parameters<PredictCoreApi['update']>[0]> = [];

  const applyUpdate = (nextParams: Parameters<PredictCoreApi['update']>[0]) => {
    if (nextParams.userProfile) {
      userProfile = nextParams.userProfile;
    }
  };

  const update: PredictCoreApi['update'] = (nextParams) => {
    if (PREDICT_TOOLS_ENABLED) {
      if (isPersonificationEnabled) {
        logDebug(
          'Skipped update because the Predict Tools extension is personifying the user.',
          nextParams
        );

        pendingUpdates.push(nextParams);

        return;
      }
    }

    applyUpdate(nextParams);

    notify('UPDATED');
  };

  const getUserProfile: PredictCoreApi['getUserProfile'] = () => userProfile;

  const getSegments: PredictCoreApi['getSegments'] = () => {
    return Object.entries(userProfile.segments || {}).reduce<Segment[]>(
      (acc, segment) => {
        // We don't use array destructuring because Babel transformations blow
        // up the bundle size. Once we have more code that requires this syntax,
        // we can consider upgrading to:
        // const [type, names] = segment; // not used for bundle optimization
        const type = segment[0];
        const names = segment[1];
        const segmentsInType = names.map<Segment>((name) => ({
          name,
          type: type as Segment['type'],
        }));

        segmentsInType.forEach((segmentInType) => {
          acc.push(segmentInType);
        });

        return acc;
      },
      []
    );
  };

  const getProperties: PredictCoreApi['getProperties'] = () => {
    return Object.entries(userProfile.properties || {}).reduce<Property[]>(
      (acc, group) => {
        // const [type, properties] = group; // not used for bundle optimization
        const type = group[0];
        const properties = group[1];

        const propertiesInType = Object.entries(properties || {}).map<Property>(
          (property) => {
            // const [key, value] = property; // not used for bundle optimization
            const key = property[0];
            const value = property[1];

            return {
              name: key,
              value,
              type: type as Property['type'],
            };
          }
        );

        propertiesInType.forEach((propertyInType) => {
          acc.push(propertyInType);
        });

        return acc;
      },
      []
    );
  };

  const getAffinities: PredictCoreApi['getAffinities'] = () => {
    return unwrapPrediction<Affinity[]>(
      userProfile.predictions,
      'affinities',
      []
    );
  };

  const getFunnelStage: PredictCoreApi['getFunnelStage'] = () => {
    return unwrapPrediction<FunnelStage[]>(
      userProfile.predictions,
      'funnel_stage',
      []
    );
  };

  const getOrderValue: PredictCoreApi['getOrderValue'] = () => {
    return unwrapPrediction<OrderValue | undefined>(
      userProfile.predictions,
      'order_value',
      undefined
    );
  };

  const getCartAbandonment: PredictCoreApi['getCartAbandonment'] = () =>
    computeCartAbandonment(getFunnelStage());

  const getZones: PredictCoreApi['getZones'] = () => zones;

  const addZone: PredictCoreApi['addZone'] = (newZone) => {
    invariant(
      !zones.some((zone) => zone.id === newZone.id),
      `Cannot add zone with ID "${newZone.id}" because it already exists.`
    );

    zones.push(newZone);
    notify('ZONE_ADDED');

    return newZone;
  };

  const removeZone: PredictCoreApi['removeZone'] = (zone) => {
    const zoneIndex = zones.findIndex((x) => x.id === zone.id);

    invariant(
      zoneIndex !== -1,
      `Cannot remove zone with ID "${zone.id}" because it doesn't exist.`
    );

    zones.splice(zoneIndex, 1);
    notify('ZONE_REMOVED');
  };

  const updateZone: PredictCoreApi['updateZone'] = (zone) => {
    const zoneIndex = zones.findIndex((x) => x.id === zone.id);

    invariant(
      zoneIndex !== -1,
      `Cannot update zone with ID "${zone.id}" because it doesn't exist.`
    );

    const prevZone = zones[zoneIndex];
    const updatedZone = {
      id: zone.id,
      name: zone.name ?? prevZone.name,
      active: zone.active ?? prevZone.active,
      happened: zone.happened ?? prevZone.happened,
    };

    zones[zoneIndex] = updatedZone;

    notify('ZONE_UPDATED');

    return updatedZone;
  };

  const subscribe: PredictCoreApi['subscribe'] = (subscriber) => {
    subscribers.push(subscriber);

    return () => {
      subscribers = subscribers.filter((x) => x !== subscriber);
    };
  };

  function notify(eventType: EventType) {
    subscribers.forEach((subscriber) => {
      subscriber({
        eventType,
        affinities: getAffinities(),
        cartAbandonment: getCartAbandonment(),
        funnelStage: getFunnelStage(),
        orderValue: getOrderValue(),
        segments: getSegments(),
        properties: getProperties(),
        userProfile: getUserProfile(),
        zones: getZones(),
      });
    });
  }

  // Predict Tools are enabled in development mode, or when the `PREDICT_TOOLS`
  // environment variable is set. This is useful to use the web extension in
  // staging environments.
  if (PREDICT_TOOLS_ENABLED) {
    initPredictTools({
      version,
      subscribe,
      getContext: () => ({
        affinities: getAffinities(),
        cartAbandonment: getCartAbandonment(),
        funnelStage: getFunnelStage(),
        orderValue: getOrderValue(),
        segments: getSegments(),
        properties: getProperties(),
        userProfile: getUserProfile(),
        zones: getZones(),
      }),
      startPersonification: ({ userProfile: userProfileToPersonify }) => {
        pendingUpdates.push({ userProfile });
        update({ userProfile: userProfileToPersonify });
        isPersonificationEnabled = true;
      },
      endPersonification: () => {
        isPersonificationEnabled = false;

        while (pendingUpdates.length > 0) {
          const updateParams = pendingUpdates.shift()!;
          applyUpdate(updateParams);
        }

        // We trigger an update to notify subscribers.
        update({});
      },
    });
  }

  const api = {
    getAffinities,
    getFunnelStage,
    getOrderValue,
    getCartAbandonment,
    getSegments,
    getProperties,
    getUserProfile,
    getZones,
    update,
    addZone,
    removeZone,
    updateZone,
    subscribe,
  };

  return api;
}
