import { type ModelError, type Predictions } from '@algolia/predict';

import { warn } from './warn';

type ValueOf<T> = T[keyof T];

function isModelError(
  prediction: Required<ValueOf<Predictions>>
): prediction is ModelError {
  return Boolean((prediction as ModelError).error);
}

/**
 * Unwraps the prediction value, or falls back when it's unavailable.
 *
 * If the model is unavailable, it returns the fallback and triggers a warning.
 */
export function unwrapPrediction<TType>(
  predictions: Predictions | undefined,
  model: keyof Predictions,
  fallback: TType
): TType {
  const prediction = predictions && predictions[model];

  if (!prediction) {
    return fallback;
  }

  if (isModelError(prediction)) {
    warn(false, prediction.error);
    return fallback;
  }

  return prediction.value as unknown as TType;
}
